.Modal{
    position: absolute;
    z-index: 4;
    justify-content: center;
    text-align: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    padding: 2rem;
    height: 80vh;
    width: 80vw;
    background-color: white;
    
}

.overlay{
    position: absolute;
    z-index: 3;
    background-color: rgba(0,0,0,.6);
    height: 100vh;
    width: 100vw;
}

.exit{
 cursor: pointer;
}